<template>
  <div v-loading="loading">
    <p style="color: red;font-size: 12px;">name3</p>
    <p style="color: red;font-size: 12px;"> v-model </p>

    <p>
      <el-button type="success" size="mini" @click="$refs.editable.insert({name: '2', age: 1, slider: 10})"></el-button>
      <el-button type="success" size="mini" @click="$refs.editable.insertAt({name: '2', age: 3, slider: 20}, -1)"></el-button>
      <el-button type="danger" size="mini" @click="deleteSelectedEvent"></el-button>
      <el-button type="info" size="mini" @click="$refs.editable.revert()"></el-button>
      <el-button type="info" size="mini" @click="$refs.editable.clear()"></el-button>
      <el-button type="info" size="mini" @click="$refs.editable.clearFilter()"></el-button>
      <el-button type="info" size="mini" @click="$refs.editable.clearSort()"></el-button>
      <el-button type="warning" size="mini" @click="submitEvent">&</el-button>
      <el-button type="primary" size="mini" @click="getInsertEvent"></el-button>
      <el-button type="primary" size="mini" @click="getUpdateEvent"></el-button>
      <el-button type="primary" size="mini" @click="getRemoveEvent"></el-button>
      <el-button type="primary" size="mini" @click="getSelectedEvent"></el-button>
      <el-button type="primary" size="mini" @click="getAllEvent"></el-button>
    </p>

    <el-editable
      ref="editable"
      stripe
      border
      height="480"
      size="medium"
      @select="selectEvent"
      @current-change="currentChangeEvent"
      :edit-rules="validRules"
      :edit-config="{trigger: 'click', mode: 'cell', showIcon: true, showStatus: true}"
      style="width: 100%">
      <el-editable-column type="selection" width="55"></el-editable-column>
      <el-editable-column type="index" width="55"></el-editable-column>
      <el-editable-column prop="name" label="" min-width="220" show-overflow-tooltip :edit-render="{name: 'ElInput'}"> </el-editable-column>
      <el-editable-column prop="sex" label="" width="100" :edit-render="{name: 'ElSelect', options: sexList}"></el-editable-column>
      <el-editable-column prop="userInfo.sex1" label="1" width="160" :edit-render="{type: 'default'}">
        <template slot="edit" slot-scope="scope">
          <el-select v-model="scope.row.userInfo.sex1" placeholder="" clearable @change="$refs.editable.updateStatus(scope)">
            <el-option v-for="(item, index) in sexList" :key="index" :value="item.val" :label="item.spell"></el-option>
          </el-select>
        </template>
        <template slot-scope="scope">{{ getSelectLabel(scope.row.userInfo.sex1, 'val', 'spell', sexList) }}</template>
      </el-editable-column>
      <el-editable-column prop="userInfo.sex2" label="2" width="160" :edit-render="{name: 'ElSelect', options: sexList, optionProps: {value: 'value', label: 'spell'}, attrs: {clearable: true, placeholder: ''}}">
        <template slot="edit" slot-scope="scope">
          <el-select v-model="scope.row.userInfo.sex2" v-bind="scope.editRender.attrs" @change="$refs.editable.updateStatus(scope)">
            <el-option v-for="(item, index) in sexList" :key="index" :value="item.value" :label="item.spell"></el-option>
          </el-select>
        </template>
      </el-editable-column>
      <el-editable-column prop="userInfo.base.age" label="" width="160" :edit-render="{name: 'ElInputNumber', attrs: {min: 1, max: 200}}">
        <template slot="edit" slot-scope="scope">
          <el-input-number v-model="scope.row.userInfo.base.age" v-bind="scope.editRender.attrs" @input="$refs.editable.updateStatus(scope)"></el-input-number>
        </template>
      </el-editable-column>
      <el-editable-column prop="userInfo.region" label="" min-width="180" :edit-render="{name: 'ElCascader', attrs: {options: regionList, separator: '-'}}">
        <template slot="edit" slot-scope="scope">
          <el-cascader v-model="scope.row.userInfo.region" v-bind="scope.editRender.attrs" @change="$refs.editable.updateStatus(scope)"></el-cascader>
        </template>
      </el-editable-column>
      <el-editable-column prop="dateObj.date1" label="" width="220" sortable :edit-render="{name: 'ElDatePicker', attrs: {type: 'datetime', format: 'yyyy-MM-dd hh:mm:ss'}}">
        <template slot="edit" slot-scope="scope">
          <el-date-picker v-model="scope.row.dateObj.date1" v-bind="scope.editRender.attrs" @change="$refs.editable.updateStatus(scope)"></el-date-picker>
        </template>
      </el-editable-column>
      <el-editable-column prop="slider" label="" width="200" :edit-render="{name: 'ElSlider', type: 'visible'}"></el-editable-column>
      <el-editable-column label="" width="180">
        <template slot-scope="scope">
          <el-button size="mini" type="danger" @click="removeEvent(scope)"></el-button>
          <el-button size="mini" type="warning" @click="revertEvent(scope.row)"></el-button>
        </template>
      </el-editable-column>
    </el-editable>
  </div>
</template>

<script>
import XEUtils from 'xe-utils'
import { MessageBox, Message } from 'element-ui'
import regionData from '@/common/json/editable/region.json'
import sexData from '@/common/json/editable/sex.json'

export default {
  data () {
    return {
      loading: false,
      sexList: [],
      regionList: [],
      validRules: {
        name: [
          { required: true, message: '', trigger: 'change' },
          { min: 3, message: ' 3 ', trigger: 'change' }
        ]
      }
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      this.findList()
      this.getSexJSON().then(data => {
        this.sexList = data
      })
      this.getRegionJSON().then(data => {
        this.regionList = data
      })
    },
    findList () {
      this.loading = true
      return this.loadList().then(data => {
        this.loading = false
      }).catch(e => {
        this.loading = false
      })
    },
    loadList () {
      return this.getDataJSON().then(data => {
        this.$refs.editable.reload(data)
      })
    },
    getSelectLabel (value, valueProp, labelProp, list) {
      let item = list.find(item => item[valueProp] === value)
      return item ? item[labelProp] : null
    },
    removeEvent (scope) {
      MessageBox.confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$refs.editable.remove(scope.row)
      }).catch(e => e)
    },
    revertEvent (row) {
      MessageBox.confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$refs.editable.revert(row)
        Message({ message: '', type: 'success' })
      }).catch(e => e)
    },
    selectEvent (selection, row) {
      console.log(selection)
    },
    currentChangeEvent (currentRow, oldCurrentRow) {
      console.log(oldCurrentRow)
    },
    deleteSelectedEvent () {
      let selection = this.$refs.editable.getSelecteds()
      if (selection.length) {
        this.postJSON('url', { selection }).then(data => {
          Message({ message: '', type: 'success' })
          this.findList()
        })
      } else {
        Message({
          type: 'info',
          message: ''
        })
      }
    },
    submitEvent () {
      this.$refs.editable.validate(valid => {
        if (valid) {
          alert('')
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    getInsertEvent () {
      let rest = this.$refs.editable.getInsertRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getUpdateEvent () {
      let rest = this.$refs.editable.getUpdateRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getRemoveEvent () {
      let rest = this.$refs.editable.getRemoveRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getSelectedEvent () {
      let rest = this.$refs.editable.getSelecteds()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getAllEvent () {
      let rest = this.$refs.editable.getRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    postJSON (data) {
      // 
      return new Promise(resolve => {
        setTimeout(() => {
          resolve('')
        }, 300)
      })
    },
    getSexJSON () {
      // 
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(sexData, true)), 100)
      })
    },
    getDataJSON () {
      // 
      return new Promise(resolve => {
        setTimeout(() => resolve([
          {
            name: '1',
            sex: '1',
            slider: 10,
            userInfo: {
              region: [1, 1, 3],
              sex1: 'o',
              sex2: '1',
              base: {
                age: 24
              }
            },
            dateObj: {
              date1: 1550588308445,
              date2: 1550588308445
            }
          },
          {
            name: '2',
            sex: '1',
            slider: 20,
            userInfo: {
              region: [1, 1, 4],
              sex1: 'x',
              sex2: '0',
              base: {
                age: 26
              }
            },
            dateObj: {
              date1: 1550588308445,
              date2: 1550588308445
            }
          },
          {
            name: '3',
            sex: '1',
            slider: 10,
            userInfo: {
              region: [1, 1, 5],
              sex1: 'o',
              sex2: '1',
              base: {
                age: 28
              }
            },
            dateObj: {
              date1: 1550588308445,
              date2: 1550588308445
            }
          }
        ]), 300)
      })
    },
    getRegionJSON () {
      // 
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(regionData, true)), 200)
      })
    }
  }
}
</script>

<style scoped>
.editable-custom_input {
  width: 95%;
}
</style>
