<template>
  <div v-loading="loading">
    <p style="color: red;font-size: 12px;"></p>

    <p>
      <el-button type="success" size="mini" @click="$refs.editable.insert({name: `New ${Date.now()}`, flag: true, createDate: Date.now()})"></el-button>
      <el-button type="success" size="mini" @click="$refs.editable.insertAt({name: `New last ${Date.now()}`, flag: true, createDate: Date.now()}, -1)"></el-button>
      <el-button type="info" size="mini" @click="$refs.editable.revert()"></el-button>
      <el-button type="info" size="mini" @click="$refs.editable.clear()"></el-button>
    </p>

    <el-editable ref="editable" :data.sync="list" border size="mini" style="width: 100%">
      <el-editable-column prop="name" label="" show-overflow-tooltip></el-editable-column>
      <el-editable-column prop="sex" label="" :edit-render="{name: 'ElSelect', options: sexList}"></el-editable-column>
      <el-editable-column prop="age" label="" :edit-render="{name: 'ElInputNumber', attrs: {min: 1, max: 200}}"></el-editable-column>
      <el-editable-column prop="region" label="" :edit-render="{name: 'ElCascader', attrs: {options: regionList}}"></el-editable-column>
      <el-editable-column prop="date1" label="" :edit-render="{name: 'ElDatePicker', attrs: {type: 'datetime', format: 'yyyy-MM-dd hh:mm:ss'}}"></el-editable-column>
      <el-editable-column prop="flag" label="" :edit-render="{name: 'ElSwitch', type: 'visible'}"></el-editable-column>
      <el-editable-column prop="attr1" label="" :edit-render="{name: 'ElInput'}"></el-editable-column>
      <el-editable-column prop="attr2" label="" :edit-render="{name: 'ElInput', attrs: {type: 'textarea', autosize: {minRows: 1, maxRows: 4}}}"></el-editable-column>
      <el-editable-column prop="createDate" label="" :formatter="formatterCreateDate"></el-editable-column>
      <el-editable-column prop="attr3" label="">
        <template>
          <a href="https://github.com/xuliangzhan/vue-element-extends" target="_blank"></a>
        </template>
      </el-editable-column>
      <el-editable-column label="">
        <template slot-scope="scope">
          <el-button size="mini" type="danger" @click="removeEvent(scope.row)"></el-button>
        </template>
      </el-editable-column>
    </el-editable>
  </div>
</template>

<script>
import XEUtils from 'xe-utils'
import { MessageBox } from 'element-ui'
import listData from '@/common/json/editable/list.json'
import regionData from '@/common/json/editable/region.json'
import sexData from '@/common/json/editable/sex.json'

export default {
  data () {
    return {
      loading: false,
      sexList: XEUtils.clone(sexData, true),
      regionList: XEUtils.clone(regionData, true),
      list: []
    }
  },
  created () {
    this.loading = true
    setTimeout(() => {
      this.list = XEUtils.clone(listData, true)
      this.loading = false
    }, 600)
  },
  methods: {
    formatterCreateDate (row, column, cellValue, index) {
      return XEUtils.toDateString(row.createDate, 'yyyy-MM-dd hh:mm:ss')
    },
    removeEvent (row) {
      MessageBox.confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$refs.editable.remove(row)
      }).catch(e => e)
    }
  }
}
</script>
