<template>
  <div>
    <p style="color: red;font-size: 12px;">name3-10flag</p>

    <p>
      <el-button type="success" size="mini" @click="$refs.editable.insert({name: '1'})"></el-button>
      <el-button type="success" size="mini" @click="$refs.editable.insertAt({name: '1'}, -1)"></el-button>
      <el-button type="danger" size="mini" @click="$refs.editable.removeSelecteds()"></el-button>
      <el-button type="info" size="mini" @click="$refs.editable.revert()"></el-button>
      <el-button type="info" size="mini" @click="$refs.editable.clear()"></el-button>
      <el-button type="warning" size="mini" @click="validEvent"></el-button>
      <el-button type="warning" size="mini" @click="submitEvent">&</el-button>
      <el-button type="info" size="mini" @click="$refs.editable.clearSelection()"></el-button>
      <el-button type="info" size="mini" @click="$refs.editable.toggleRowSelection($refs.editable.getRecords(1), true)"></el-button>
      <el-button type="info" size="mini" @click="$refs.editable.toggleAllSelection()"></el-button>
    </p>

    <el-editable
      ref="editable"
      size="mini"
      border
      :data.sync="list"
      :edit-rules="validRules"
      :edit-config="{trigger: 'manual', mode: 'row'}"
      style="width: 100%">
      <el-editable-column type="selection" width="55"></el-editable-column>
      <el-editable-column prop="sex" label="" :edit-render="{name: 'ElSelect', options: sexList}"></el-editable-column>
      <el-editable-column prop="age" label="" :edit-render="{name: 'ElInputNumber', attrs: {min: 1, max: 200}}"></el-editable-column>
      <el-editable-column prop="name" label="" show-overflow-tooltip :edit-render="{name: 'ElInput'}"></el-editable-column>
      <el-editable-column prop="region" label="" :edit-render="{name: 'ElCascader', attrs: {options: regionList}}"></el-editable-column>
      <el-editable-column prop="birthdate" label="" :edit-render="{name: 'ElDatePicker', attrs: {type: 'date', format: 'yyyy-MM-dd'}}"></el-editable-column>
      <el-editable-column prop="date1" label="" :edit-render="{name: 'ElDatePicker', attrs: {type: 'datetime', format: 'yyyy-MM-dd hh:mm:ss'}}"></el-editable-column>
      <el-editable-column prop="flag" label="" :edit-render="{name: 'ElSwitch', type: 'visible'}"></el-editable-column>
      <el-editable-column prop="remark" label="" :edit-render="{name: 'ElInput'}"></el-editable-column>
      <el-editable-column label="" width="220">
        <template slot-scope="scope">
          <template v-if="$refs.editable.isActiveRow(scope.row)">
            <el-button size="mini" type="success" @click="saveRowEvent(scope.row)"></el-button>
            <el-button size="mini" type="warning" @click="cancelRowEvent(scope.row)"></el-button>
            <el-button size="mini" type="warning" @click="$refs.editable.revert(scope.row)"></el-button>
          </template>
          <template v-else>
            <el-button size="mini" type="primary" @click="$refs.editable.setActiveRow(scope.row)"></el-button>
            <el-button size="mini" type="danger" @click="removeEvent(scope.row)"></el-button>
            <el-button size="mini" type="warning" @click="revertEvent(scope.row)"></el-button>
          </template>
        </template>
      </el-editable-column>
    </el-editable>
  </div>
</template>

<script>
import XEUtils from 'xe-utils'
import { Message, MessageBox } from 'element-ui'
import listData from '@/common/json/editable/list.json'
import regionData from '@/common/json/editable/region.json'
import sexData from '@/common/json/editable/sex.json'

export default {
  data () {
    return {
      sexList: XEUtils.clone(sexData, true),
      regionList: XEUtils.clone(regionData, true),
      list: XEUtils.clone(listData, true),
      validRules: {
        name: [
          { required: true, message: '', trigger: 'change' },
          { min: 3, max: 10, message: ' 3-10 ', trigger: 'blur' }
        ],
        flag: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    removeEvent (row) {
      MessageBox.confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$refs.editable.remove(row)
      }).catch(e => e)
    },
    revertEvent (row) {
      if (this.$refs.editable.isRowChange(row)) {
        MessageBox.confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$refs.editable.revert(row)
          Message({ message: '', type: 'success' })
        }).catch(e => e)
      } else {
        Message({ message: '', type: 'info' })
      }
    },
    saveRowEvent (row) {
      this.$refs.editable.validateRow(row, valid => {
        if (valid) {
          this.$refs.editable.clearActive()
          Message({ message: '', type: 'success' })
        } else {
          console.log('error row submit!!')
        }
      })
    },
    cancelRowEvent (row) {
      this.$refs.editable.validateRow(row, (valid, validErrs) => {
        if (valid) {
          this.$refs.editable.clearActive()
        } else {
          let message = <p>
            <p></p>
            {
              Object.keys(validErrs).map(name => {
                let errors = validErrs[name]
                let msg = `${name}${errors.map(e => e.message).join(';')}`
                return <p>{msg}</p>
              })
            }
          </p>
          Message({ message, dangerouslyUseHTMLString: true, type: 'error' })
        }
      })
    },
    validEvent () {
      this.$refs.editable.validate().then(valid => {
        Message({ message: '', type: 'success' })
      }).catch(valid => {
        console.log('error submit!!')
      })
    },
    submitEvent () {
      this.$refs.editable.validate().then(valid => {
        alert('')
      }).catch(valid => {
        console.log('error submit!!')
      })
    }
  }
}
</script>
