<template>
  <div v-loading="loading">
    <p style="color: red;font-size: 12px;"></p>
    <p style="color: red;font-size: 12px;">name3-10attr1attr2sexage18-28rate3</p>

    <p>
      <el-button type="success" size="mini" @click="insertEvent(0)"></el-button>
      <el-button type="success" size="mini" @click="insertEvent(-1)"></el-button>
      <el-button type="danger" size="mini" @click="$refs.editable.removeSelecteds()"></el-button>
      <el-button type="info" size="mini" @click="$refs.editable.revert()"></el-button>
      <el-button type="info" size="mini" @click="$refs.editable.clear()"></el-button>
      <el-button type="warning" size="mini" @click="validEvent"></el-button>
      <el-button type="warning" size="mini" @click="submitEvent">&</el-button>
      <el-button type="primary" size="mini" @click="getInsertEvent"></el-button>
      <el-button type="primary" size="mini" @click="getUpdateEvent"></el-button>
      <el-button type="primary" size="mini" @click="getRemoveEvent"></el-button>
      <el-button type="primary" size="mini" @click="getAllEvent"></el-button>
    </p>

    <el-editable
      ref="editable"
      stripe
      border
      size="medium"
      height="480"
      :edit-rules="validRules"
      style="width: 100%">
      <el-editable-column type="index" width="55"></el-editable-column>
      <el-editable-column prop="name" label="" show-overflow-tooltip :edit-render="{name: 'ElInput'}"></el-editable-column>
      <el-editable-column prop="attr1" label="1" :edit-render="{name: 'ElInput'}"></el-editable-column>
      <el-editable-column prop="attr2" label="2()" :edit-render="{name: 'ElInput'}"></el-editable-column>
      <el-editable-column prop="birthdate" label="" width="220" :edit-render="{name: 'ElDatePicker', attrs: {type: 'date', format: 'yyyy-MM-dd'}}"></el-editable-column>
      <el-editable-column prop="sex" label="" :edit-render="{name: 'ElSelect', options: sexList}"></el-editable-column>
      <el-editable-column prop="rate" label="" :edit-render="{name: 'ElRate', type: 'visible'}"></el-editable-column>
      <el-editable-column label="">
        <template slot-scope="scope">
          <el-button size="mini" type="danger" @click="removeEvent(scope.row)"></el-button>
        </template>
      </el-editable-column>
    </el-editable>
  </div>
</template>

<script>
import XEUtils from 'xe-utils'
import { MessageBox } from 'element-ui'
import listData from '@/common/json/editable/list.json'
import regionData from '@/common/json/editable/region.json'
import sexData from '@/common/json/editable/sex.json'

export default {
  data () {
    const checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error(''))
        } else {
          if (parseInt(value) < 18 || parseInt(value) > 28) {
            callback(new Error('18-28'))
          } else {
            callback()
          }
        }
      }, 50)
    }
    const checkRate = (rule, value, callback) => {
      if (parseInt(value || 0) < 3) {
        callback(new Error('3'))
      } else {
        callback()
      }
    }
    return {
      loading: false,
      columnConfigs: [],
      sexList: [],
      regionList: [],
      validRules: {
        name: [
          { required: true, message: '', trigger: 'change' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'change' }
        ],
        sex: [
          { required: true, message: '', trigger: 'blur' }
        ],
        age: [
          { validator: checkAge, trigger: 'blur' }
        ],
        rate: [
          { validator: checkRate, trigger: 'blur' }
        ],
        attr1: [
          { type: 'number', message: '', trigger: 'blur' }
        ],
        attr2: [
          { required: true, type: 'number', message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      this.findList()
      this.getSexJSON().then(data => {
        this.sexList = data
      })
      this.getRegionJSON().then(data => {
        this.regionList = data
      })
    },
    findList () {
      this.loading = true
      this.getDataJSON().then(data => {
        this.$refs.editable.reload(data)
        this.loading = false
      }).catch(e => {
        this.loading = false
      })
    },
    insertEvent (index) {
      if (!this.$refs.editable.checkValid().error) {
        this.$refs.editable.insertAt(null, index)
      }
    },
    removeEvent (row) {
      this.$refs.editable.remove(row)
    },
    validEvent () {
      this.$refs.editable.validate().then(valid => {
        alert('')
      }).catch(valid => {
        console.log('error submit!!')
      })
    },
    submitEvent () {
      this.$refs.editable.validate().then(valid => {
        let { insertRecords, removeRecords, updateRecords } = this.$refs.editable.getAllRecords()
        this.postJSON('url', { insertRecords, removeRecords, updateRecords }).then(data => {
          this.findList()
        })
      }).catch(valid => {
        console.log('error submit!!')
      })
    },
    getInsertEvent () {
      let rest = this.$refs.editable.getInsertRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getUpdateEvent () {
      let rest = this.$refs.editable.getUpdateRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getRemoveEvent () {
      let rest = this.$refs.editable.getRemoveRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getAllEvent () {
      let rest = this.$refs.editable.getRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    postJSON (data) {
      // 
      return new Promise(resolve => {
        setTimeout(() => {
          resolve('')
        }, 300)
      })
    },
    getSexJSON () {
      // 
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(sexData, true)), 200)
      })
    },
    getDataJSON () {
      // 
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(listData, true)), 400)
      })
    },
    getRegionJSON () {
      // 
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(regionData, true)), 200)
      })
    }
  }
}
</script>
