<template>
  <div v-loading="loading">
    <p style="color: red;font-size: 12px;">name3-10nickname5-20sexage18-28rate2urlURLattr1attr2attr3</p>

    <p>
      <el-button type="success" size="mini" @click="$refs.editable.insert()"></el-button>
      <el-button type="success" size="mini" @click="$refs.editable.insertAt(null, -1)"></el-button>
      <el-button type="danger" size="mini" @click="$refs.editable.removeSelecteds()"></el-button>
      <el-button type="info" size="mini" @click="$refs.editable.revert()"></el-button>
      <el-button type="info" size="mini" @click="$refs.editable.clear()"></el-button>
      <el-button type="warning" size="mini" @click="validEvent"></el-button>
      <el-button type="warning" size="mini" @click="submitEvent">&</el-button>
      <el-button type="primary" size="mini" @click="getInsertEvent"></el-button>
      <el-button type="primary" size="mini" @click="getUpdateEvent"></el-button>
      <el-button type="primary" size="mini" @click="getRemoveEvent"></el-button>
      <el-button type="primary" size="mini" @click="getAllEvent"></el-button>
    </p>

    <el-editable
      ref="editable"
      class="my-table11"
      stripe
      border
      size="medium"
      height="480"
      :edit-rules="validRules"
      :edit-config="{trigger: 'dblclick', showIcon: false, showStatus: false, validTooltip: { placement: 'right', popperClass: 'dbl3-validtip' }}"
      style="width: 100%" >
      <el-editable-column type="index" width="55">
        <template slot="head">
          <i class="el-icon-setting" @click="dialogVisible = true"></i>
        </template>
      </el-editable-column>
      <template v-for="(item, index) in columnConfigs">
        <template v-if="item.customShow">
          <el-editable-column v-if="index === 0" :key="index" v-bind="item">
            <template slot="valid" slot-scope="scope"><br>{{ scope.rule.message }}<br><br><a href="https://github.com/xuliangzhan/vue-element-extends" target="_blank">API</a></template>
          </el-editable-column>
          <el-editable-column v-else :key="index" v-bind="item"></el-editable-column>
        </template>
      </template>
      <el-editable-column label="">
        <template slot-scope="scope">
          <el-button size="mini" type="danger" @click="removeEvent(scope.row)"></el-button>
          <el-button size="mini" type="warning" @click="revertEvent(scope.row)"></el-button>
        </template>
      </el-editable-column>
    </el-editable>

    <el-dialog title="" :visible.sync="dialogVisible" width="300px" @open="openCustomEvent">
      <ul>
        <li v-for="(item, index) in columnConfigs" :key="index">
          <el-checkbox v-model="item.customChecked">{{ item.label }}</el-checkbox>
        </li>
      </ul>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetCustomEvent"> </el-button>
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveCustomEvent"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import XEUtils from 'xe-utils'
import { MessageBox, Message } from 'element-ui'
import listData from '@/common/json/editable/list.json'
import regionData from '@/common/json/editable/region.json'
import sexData from '@/common/json/editable/sex.json'
import columnsData from '@/common/json/editable/columns.json'

export default {
  data () {
    const checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error(''))
        } else {
          if (parseInt(value) < 18 || parseInt(value) > 28) {
            callback(new Error('18-28'))
          } else {
            callback()
          }
        }
      }, 50)
    }
    const checkRate = (rule, value, callback) => {
      if (parseInt(value || 0) < 2) {
        callback(new Error('2'))
      } else {
        callback()
      }
    }
    const checkURL = (rule, value, callback) => {
      if (!value || /^((ht|f)tps?):\/\/([\w-]+(\.[\w-]+)*\/)*[\w-]+(\.[\w-]+)*\/?(\?([\w\-.,@?^=%&:/~+#]*)+)?/.test(value)) {
        callback()
      } else {
        callback(new Error('http(s)://xxx.com'))
      }
    }
    const checkAttr2 = (rule, value, callback) => {
      if (!value || XEUtils.isInteger(Number(value))) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    const checkAttr3 = (rule, value, callback) => {
      if (!value || XEUtils.isFloat(Number(value))) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    return {
      loading: false,
      dialogVisible: false,
      columnConfigs: [],
      sexList: [],
      regionList: [],
      validRules: {
        name: [
          { required: true, message: '', trigger: 'change' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'change' }
        ],
        nickname: [
          { min: 5, max: 20, message: ' 5  20 ', trigger: 'blur' }
        ],
        sex: [
          { required: true, message: '', trigger: 'blur' }
        ],
        age: [
          { validator: checkAge, trigger: 'blur' }
        ],
        birthdate: [
          { required: true, message: '', trigger: 'blur' }
        ],
        rate: [
          { validator: checkRate, trigger: 'blur' }
        ],
        url: [
          { validator: checkURL, trigger: 'change' }
        ],
        attr1: [
          { type: 'number', message: '', trigger: 'change' }
        ],
        attr2: [
          { validator: checkAttr2, trigger: 'change' }
        ],
        attr3: [
          { validator: checkAttr3, trigger: 'change' }
        ]
      }
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      let sexPromise = this.getSexJSON()
      let regionPromise = this.getRegionJSON()
      this.loading = true
      Promise.all([
        this.loadList(),
        this.getColumnConfigs().then(data => {
          this.columnConfigs = data.map(column => {
            let defaultShow = ['name', 'nickname', 'region', 'rate'].includes(column.prop)
            column.customDefault = defaultShow
            column.customChecked = defaultShow
            column.customShow = defaultShow
            switch (column.prop) {
              case 'sex':
                column.editRender.options = []
                sexPromise.then(rest => {
                  column.editRender.options = rest
                })
                break
              case 'region':
                column.editRender.attrs = { options: [] }
                regionPromise.then(rest => {
                  column.editRender.attrs.options = rest
                })
                break
              case 'birthdate':
                column.editRender.attrs = {
                  type: 'date',
                  format: 'yyyy-MM-dd'
                }
                break
              case 'rate':
                column.minWidth = '90'
                column.editRender.type = 'visible'
                break
            }
            return column
          })
        })
      ]).then(datas => {
        this.loading = false
      }).catch(e => {
        this.loading = false
      })
    },
    findList () {
      this.loading = true
      return this.loadList().then(data => {
        this.loading = false
      }).catch(e => {
        this.loading = false
      })
    },
    loadList () {
      return this.getDataJSON().then(data => {
        this.$refs.editable.reload(data)
      })
    },
    removeEvent (row) {
      this.$refs.editable.remove(row)
    },
    revertEvent (row) {
      if (this.$refs.editable.isRowChange(row)) {
        MessageBox.confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$refs.editable.revert(row)
          Message({ message: '', type: 'success' })
        }).catch(e => e)
      } else {
        Message({ message: '', type: 'info' })
      }
    },
    validEvent () {
      this.$refs.editable.validate().then(valid => {
        alert('')
      }).catch(valid => {
        console.log('error submit!!')
      })
    },
    submitEvent () {
      this.$refs.editable.validate().then(valid => {
        let { insertRecords, removeRecords, updateRecords } = this.$refs.editable.getAllRecords()
        this.postJSON('url', { insertRecords, removeRecords, updateRecords }).then(data => {
          this.findList()
        })
      }).catch(valid => {
        console.log('error submit!!')
      })
    },
    openCustomEvent () {
      this.columnConfigs.forEach(column => {
        column.customChecked = column.customShow
      })
    },
    resetCustomEvent () {
      this.columnConfigs.forEach(column => {
        column.customChecked = column.customDefault
      })
    },
    saveCustomEvent () {
      this.dialogVisible = false
      this.columnConfigs.forEach(column => {
        column.customShow = column.customChecked
      })
    },
    getInsertEvent () {
      let rest = this.$refs.editable.getInsertRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getUpdateEvent () {
      let rest = this.$refs.editable.getUpdateRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getRemoveEvent () {
      let rest = this.$refs.editable.getRemoveRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getSelectedEvent () {
      let rest = this.$refs.editable.getSelecteds()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getAllEvent () {
      let rest = this.$refs.editable.getRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    postJSON (data) {
      // 
      return new Promise(resolve => {
        setTimeout(() => {
          resolve('')
        }, 300)
      })
    },
    getSexJSON () {
      // 
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(sexData, true)), 100)
      })
    },
    getColumnConfigs () {
      // 
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(columnsData, true)), 800)
      })
    },
    getDataJSON () {
      // 
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(listData, true)), 400)
      })
    },
    getRegionJSON () {
      // 
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(regionData, true)), 200)
      })
    }
  }
}
</script>

<style>
.el-tooltip__popper.editable-valid_tooltip.dbl3-validtip {
  background: red;
}
.el-tooltip__popper.editable-valid_tooltip.dbl3-validtip[x-placement^=right] .popper__arrow,
.el-tooltip__popper.editable-valid_tooltip.dbl3-validtip[x-placement^=right] .popper__arrow::after {
  border-right-color: red;
}
</style>
