<template>
  <div v-loading="loading">
    <p style="color: red;font-size: 12px;"></p>
    <p style="color: red;font-size: 12px;">name3-10nickname5-20sexage18-28phonerate2urlURLattr1attr2attr3</p>
    <p style="color: red;font-size: 12px;">attr4attr5</p>

    <p>
      <el-button type="success" size="mini" @click="insertEvent"></el-button>
      <el-button type="danger" size="mini" @click="$refs.editable.removeSelecteds()"></el-button>
      <el-button type="info" size="mini" @click="$refs.editable.revert()"></el-button>
      <el-button type="info" size="mini" @click="$refs.editable.clear()"></el-button>
      <el-button type="warning" size="mini" @click="validEvent"></el-button>
      <el-button type="warning" size="mini" @click="submitEvent">&</el-button>
      <el-button type="primary" size="mini" @click="getInsertEvent"></el-button>
      <el-button type="primary" size="mini" @click="getUpdateEvent"></el-button>
      <el-button type="primary" size="mini" @click="getRemoveEvent"></el-button>
      <el-button type="primary" size="mini" @click="getAllEvent"></el-button>
    </p>

    <el-editable
      ref="editable"
      class="my-table11"
      stripe
      border
      size="medium"
      :edit-rules="validRules"
      :edit-config="{trigger: 'dblclick', showIcon: false, showStatus: false}"
      style="width: 100%">
      <el-editable-column type="index" width="55">
        <template slot="head">
          <i class="el-icon-setting" @click="dialogVisible = true"></i>
        </template>
      </el-editable-column>
      <template v-for="(item, index) in columnConfigs">
        <template v-if="item.customShow">
          <el-editable-column v-if="index === 0" :key="index" v-bind="item">
            <template slot="head" slot-scope="scope">
              <i class="editable-required-icon"></i>
              <i class="el-icon-edit editable-header-icon"></i>
              {{ scope.column.label }}
            </template>
          </el-editable-column>
          <el-editable-column v-else-if="item.prop === 'attr4'" :key="index" v-bind="item">
            <template slot="head" slot-scope="scope">
              <i class="editable-required-icon"></i>
              {{ scope.column.label }}
              <el-tooltip class="item" placement="top">
                <div slot="content"><br>attr4<br></div>
                <i class="el-icon-question"></i>
              </el-tooltip>
            </template>
          </el-editable-column>
          <el-editable-column v-else-if="item.prop === 'attr5'" :key="index" v-bind="item">
            <template slot="head" slot-scope="scope">
              {{ scope.column.label }}
              <el-tooltip class="item" content="attr5" placement="top">
                <i class="el-icon-warning"></i>
              </el-tooltip>
            </template>
          </el-editable-column>
          <el-editable-column v-else :key="index" v-bind="item">
            <template slot="head" slot-scope="scope">
              <i class="el-icon-edit editable-header-icon"></i>
              {{ scope.column.label }}
            </template>
          </el-editable-column>
        </template>
      </template>
      <el-editable-column label="">
        <template slot-scope="scope">
          <el-button size="mini" type="danger" @click="removeEvent(scope)"></el-button>
        </template>
      </el-editable-column>
    </el-editable>

    <el-dialog title="" :visible.sync="dialogVisible" width="300px" @open="openCustomEvent">
      <ul>
        <li v-for="(item, index) in columnConfigs" :key="index">
          <el-checkbox v-model="item.customChecked">{{ item.label }}</el-checkbox>
        </li>
      </ul>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetCustomEvent"> </el-button>
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveCustomEvent"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import XEUtils from 'xe-utils'
import { MessageBox } from 'element-ui'
import regionData from '@/common/json/editable/region.json'
import sexData from '@/common/json/editable/sex.json'
import columnsData from '@/common/json/editable/columns.json'

export default {
  data () {
    const checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error(''))
        } else {
          if (parseInt(value) < 18 || parseInt(value) > 28) {
            callback(new Error('18-28'))
          } else {
            callback()
          }
        }
      }, 50)
    }
    const checkRate = (rule, value, callback) => {
      if (parseInt(value || 0) < 2) {
        callback(new Error('2'))
      } else {
        callback()
      }
    }
    const checkAttr2 = (rule, value, callback) => {
      if (!value || XEUtils.isInteger(Number(value))) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    const checkAttr3 = (rule, value, callback) => {
      if (!value || XEUtils.isFloat(Number(value))) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    return {
      loading: false,
      dialogVisible: false,
      columnConfigs: [],
      sexList: [],
      regionList: [],
      validRules: {
        name: [
          { required: true, message: '', trigger: 'change' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'change' }
        ],
        nickname: [
          { min: 5, max: 20, message: ' 5  20 ', trigger: 'blur' }
        ],
        sex: [
          { required: true, message: '', trigger: 'blur' }
        ],
        age: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkAge, trigger: 'blur' }
        ],
        phone: [
          { required: true, message: '', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '', trigger: 'blur' }
        ],
        birthdate: [
          { required: true, message: '', trigger: 'blur' }
        ],
        rate: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkRate, trigger: 'blur' }
        ],
        url: [
          { required: true, message: 'URL', trigger: 'change' },
          { pattern: /^((ht|f)tps?):\/\/([\w-]+(\.[\w-]+)*\/)*[\w-]+(\.[\w-]+)*\/?(\?([\w\-.,@?^=%&:/~+#]*)+)?/, message: 'http(s)://xxx.com', trigger: 'change' }
        ],
        attr1: [
          { type: 'number', message: '', trigger: 'change' }
        ],
        attr2: [
          { validator: checkAttr2, trigger: 'change' }
        ],
        attr3: [
          { validator: checkAttr3, trigger: 'change' }
        ],
        attr4: [
          { required: true, message: 'attr4', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      let sexPromise = this.getSexJSON()
      let regionPromise = this.getRegionJSON()
      this.loading = true
      Promise.all([
        this.loadList(),
        this.getColumnConfigs().then(data => {
          this.columnConfigs = data.map(column => {
            let defaultShow = ['name', 'nickname', 'sex', 'region', 'phone', 'rate', 'attr1', 'attr2', 'attr3', 'attr4', 'attr5'].includes(column.prop)
            let editRender = column.editRender
            column.customDefault = defaultShow
            column.customChecked = defaultShow
            column.customShow = defaultShow
            column.minWidth = '150'
            editRender.attrs = {
              placeholder: `${column.label}`
            }
            switch (column.prop) {
              case 'sex':
                editRender.options = []
                sexPromise.then(rest => {
                  editRender.options = rest
                })
                break
              case 'region':
                editRender.attrs = { options: [] }
                regionPromise.then(rest => {
                  editRender.attrs.options = rest
                })
                break
              case 'birthdate':
                editRender.attrs = {
                  type: 'date',
                  format: 'yyyy-MM-dd'
                }
                break
              case 'rate':
                editRender.type = 'visible'
                break
            }
            return column
          })
        })
      ]).then(datas => {
        this.loading = false
      }).catch(e => {
        this.loading = false
      })
    },
    findList () {
      this.loading = true
      return this.loadList().then(data => {
        this.loading = false
      }).catch(e => {
        this.loading = false
      })
    },
    loadList () {
      return this.getDataJSON().then(data => {
        this.$refs.editable.reload(data)
      })
    },
    insertEvent () {
      if (!this.$refs.editable.checkValid().error) {
        let row = this.$refs.editable.insert({ region: [19, 199, 1773] })
        // 
        this.$nextTick(() => this.$refs.editable.validateRow(row).catch(e => e))
      }
    },
    removeEvent (scope) {
      MessageBox.confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$refs.editable.remove(scope.row)
      }).catch(e => e)
    },
    validEvent () {
      this.$refs.editable.validate().then(valid => {
        alert('')
      }).catch(valid => {
        console.log('error submit!!')
      })
    },
    submitEvent () {
      this.$refs.editable.validate().then(valid => {
        let { insertRecords, removeRecords, updateRecords } = this.$refs.editable.getAllRecords()
        this.postJSON('url', { insertRecords, removeRecords, updateRecords }).then(data => {
          this.findList()
        })
      }).catch(valid => {
        console.log('error submit!!')
      })
    },
    openCustomEvent () {
      this.columnConfigs.forEach(column => {
        column.customChecked = column.customShow
      })
    },
    resetCustomEvent () {
      this.columnConfigs.forEach(column => {
        column.customChecked = column.customDefault
      })
    },
    saveCustomEvent () {
      this.dialogVisible = false
      this.columnConfigs.forEach(column => {
        column.customShow = column.customChecked
      })
    },
    getInsertEvent () {
      let rest = this.$refs.editable.getInsertRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getUpdateEvent () {
      let rest = this.$refs.editable.getUpdateRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getRemoveEvent () {
      let rest = this.$refs.editable.getRemoveRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getSelectedEvent () {
      let rest = this.$refs.editable.getSelecteds()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    getAllEvent () {
      let rest = this.$refs.editable.getRecords()
      MessageBox({ message: JSON.stringify(rest), title: `(${rest.length})` })
    },
    postJSON (data) {
      // 
      return new Promise(resolve => {
        setTimeout(() => {
          resolve('')
        }, 300)
      })
    },
    getSexJSON () {
      // 
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(sexData, true)), 300)
      })
    },
    getColumnConfigs () {
      // 
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(columnsData, true)), 600)
      })
    },
    getDataJSON () {
      // 
      return new Promise(resolve => {
        setTimeout(() => resolve([]), 350)
      })
    },
    getRegionJSON () {
      // 
      return new Promise(resolve => {
        setTimeout(() => resolve(XEUtils.clone(regionData, true)), 200)
      })
    }
  }
}
</script>

<style scoped>
.my-table11 .error-msg {
  display: block;
  color: #fff;
  background-color: red;
  border-radius: 8px;
  font-size: 12px;
  line-height: 1;
  padding: 10px;
  position: absolute;
  top: calc(100% + 10px);
  left: 10px;
  z-index: 9;
}
.my-table11 .error-msg:before {
  content: "";
  position: absolute;
  border: 4px solid;
  top: -8px;
  left: 20%;
  border-color: transparent transparent red transparent;
}
</style>
